import { Component, Vue, Inject } from 'vue-property-decorator';

import { required } from 'vuelidate/lib/validators';

import { IMission, Mission } from '@/shared/model/mission.model';
import MissionService from './mission.service';

const validations: any = {
  mission: {
    name: {
      required,
    },
    description: {},
  },
};

@Component({
  validations,
})
export default class MissionUpdate extends Vue {
  @Inject('missionService') private missionService: () => MissionService;
  public mission: IMission = new Mission();
  public isSaving = false;
  public currentLanguage = '';

  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (to.params.missionId) {
        vm.retrieveMission(to.params.missionId);
      }
    });
  }

  created(): void {
    this.currentLanguage = this.$store.getters.currentLanguage;
    this.$store.watch(
      () => this.$store.getters.currentLanguage,
      () => {
        this.currentLanguage = this.$store.getters.currentLanguage;
      }
    );
  }

  public save(): void {
    this.isSaving = true;
    if (this.mission.id) {
      this.missionService()
        .update(this.mission)
        .then(param => {
          this.isSaving = false;
          this.$router.go(-1);
          const message = this.$t('space1App.mission.updated', { param: param.id });
          return this.$root.$bvToast.toast(message.toString(), {
            toaster: 'b-toaster-top-center',
            title: 'Info',
            variant: 'info',
            solid: true,
            autoHideDelay: 5000,
          });
        });
    } else {
      this.missionService()
        .create(this.mission)
        .then(param => {
          this.isSaving = false;
          this.$router.go(-1);
          const message = this.$t('space1App.mission.created', { param: param.id });
          this.$root.$bvToast.toast(message.toString(), {
            toaster: 'b-toaster-top-center',
            title: 'Success',
            variant: 'success',
            solid: true,
            autoHideDelay: 5000,
          });
        });
    }
  }

  public retrieveMission(missionId): void {
    this.missionService()
      .find(missionId)
      .then(res => {
        this.mission = res;
      });
  }

  public previousState(): void {
    this.$router.go(-1);
  }

  public initRelationships(): void {}
}
